import React, { useRef } from "react";
import { Canvas, useFrame } from "@react-three/fiber";
import { useGLTF, Stage, PresentationControls } from "@react-three/drei";

export default function Planet(props) {
  const { name, rotateSpeed, orbitRadius, orbitSpeed } = props;
  const { scene } = useGLTF(`${process.env.GATSBY_PUBLIC_URL}/models/solarsystem/${name}.glb`);
  const planetRef = useRef();

  const handleClick = () => {
    window.location.href = `${process.env.GATSBY_PUBLIC_URL}/etc`; 
  };

  useFrame((state) => {
    // Rotate the model around its axes
    if (planetRef.current) {
      const elapsedTime = state.clock.getElapsedTime(); // Get the elapsed time

      // Update the position based on the orbit equation
      planetRef.current.position.x = Math.cos(elapsedTime * orbitSpeed) * orbitRadius;
      planetRef.current.position.z = Math.sin(elapsedTime * orbitSpeed) * orbitRadius;
      planetRef.current.rotation.y -= rotateSpeed;
    }
  });

  return <primitive onClick={handleClick} object={scene} ref={planetRef} {...props} />
}
